import { Injectable } from '@angular/core';

@Injectable()
export class AuthService {

  constructor() { }
  loggedIn = false;
  
    isAuthenticated() {
      const promise = new Promise(
        (resolve, reject) => {
          setTimeout(() => {
            resolve(this.loggedIn);
          }, 800);
        }
      );
      return promise;
    }
  
    login() {
      this.loggedIn = true;
      console.log(this.loggedIn," login")
    }
  
    logout() {
      this.loggedIn = false;
    }
}



